import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { inject, observer} from 'mobx-react';
import AppStore from '../../store/AppStore2';
import loadable from '@loadable/component';
import { LazyRenderer, EmptyComponent } from './../../utility/lazyRenderer';

import Page2Card1 from './subComponent/page2/page2card1';
import Page2Card2 from './subComponent/page2/page2card2';
import Page2Card3 from './subComponent/page2/page2card3';
import Page2Card4 from './subComponent/page2/page2card4';
import Page2Card5 from './subComponent/page2/page2card5';
import Page2Card6 from './subComponent/page2/page2card6';
import Page2Card7 from './subComponent/page2/page2card7';
// import Page2Card8 from './subComponent/page2/page2card8';
const Page2Card8 = loadable(() => import(/* webpackChunkName: "page2card8" */ './subComponent/page2/page2card8'), {fallback: <EmptyComponent/>})
import Page2Card9 from './subComponent/page2/page2card9';
import Page2Card10 from './subComponent/page2/page2card10';



@inject('appStore')
@observer
class LandingPage2 extends Component {
  constructor(props) {
    super(props);
}

static propTypes = {
    appStore: PropTypes.object.isRequired,
};

static defaultProps = {
    appStore: new AppStore(),
};

  componentDidMount(){
    if(typeof(landingPageDidMount)!=="undefined"){
      landingPageDidMount=true;
    }
  }
  
  render() {
    const { data } = this.props.appStore;
    return (
      <div>
        {!data.cards[0].loadError && <Page2Card1 data={data.cards[0]} lang={data.lang} appStore={this.props.appStore}></Page2Card1>}
{!data.cards[1].loadError && <Page2Card2 data={data.cards[1]} lang={data.lang} appStore={this.props.appStore}></Page2Card2>}
{!data.cards[2].loadError && <Page2Card3 data={data.cards[2]} lang={data.lang} appStore={this.props.appStore}></Page2Card3>}
{!data.cards[3].loadError && <Page2Card4 data={data.cards[3]} lang={data.lang} appStore={this.props.appStore}></Page2Card4>}
{!data.cards[4].loadError && <Page2Card5 data={data.cards[4]} lang={data.lang} appStore={this.props.appStore}></Page2Card5>}
{!data.cards[5].loadError && <Page2Card6 data={data.cards[5]} lang={data.lang} appStore={this.props.appStore}></Page2Card6>}
{!data.cards[6].loadError && <Page2Card7 data={data.cards[6]} lang={data.lang} appStore={this.props.appStore}></Page2Card7>}
{!data.cards[7].loadError && <LazyRenderer id={data.cards[7]?.attributes?.sectionId}><Page2Card8 data={data.cards[7]} lang={data.lang} appStore={this.props.appStore}></Page2Card8></LazyRenderer>}
{!data.cards[8].loadError && <Page2Card9 data={data.cards[8]} lang={data.lang} appStore={this.props.appStore}></Page2Card9>}
{!data.cards[9].loadError && <Page2Card10 data={data.cards[9]} lang={data.lang} appStore={this.props.appStore}></Page2Card10>}

      </div>
    )
  }
}

export default LandingPage2;
